<script lang="ts">
  import { wallet } from "~/store";
  import { onMount } from "svelte";
  import { nimbus } from "~/lib/network";
  import { createQuery } from "@tanstack/svelte-query";
  import { t } from "~/lib/i18n";

  import ErrorBoundary from "~/components/ErrorBoundary.svelte";
  import Holding from "~/UI/YieldFarming/Holding.svelte";
  import YieldTable from "~/UI/YieldFarming/YieldTable.svelte";
  import Filter from "~/UI/YieldFarming/Filter.svelte";

  let selectedTVL: any = "";
  let selectedAPY: any = "";
  let selectedRisk: any = "";
  let selectedTag: string[] = [];

  const handleChangeFilter = (data: any) => {
    selectedTVL = data.selectedTVL;
    selectedAPY = data.selectedAPY;
    selectedRisk = data.selectedRisk;
    selectedTag = data.selectedTag;
  };

  let selectedChain = "SUI";
  let selectedTokenList: any = [];

  let orderBy = "apy:desc";

  onMount(() => {
    const urlParams = new URLSearchParams(window.location.search);
    const chainParams = urlParams.get("chain");

    if (chainParams) {
      selectedChain = chainParams;
    }
  });

  const handleQueryListToken = async (chain: string) => {
    const response = await nimbus
      .get(`/onchain/coins?chain=${chain}`)
      .then((res) => res?.data);
    return response;
  };

  $: queryListToken = createQuery({
    queryKey: ["query-list-token-by-chain", selectedChain],
    queryFn: () => handleQueryListToken(selectedChain),
    retry: false,
    enabled: Boolean(selectedChain && selectedChain?.length !== 0),
  });

  $: {
    if (!$queryListToken.isError && $queryListToken.data !== undefined) {
      selectedTokenList = $queryListToken.data;
    }
  }

  let selectedToken = {};

  let dataVaults: any = [];

  const handleSelectedToken = (tokenInfo: any) => {
    selectedToken = tokenInfo;
  };

  let selectedWallet: any = {};

  const handleSelectedAddress = (selectedWalletInfo: any) => {
    selectedWallet = selectedWalletInfo;
    wallet.update((n) => (n = selectedWalletInfo?.value));
    selectedToken = {};
  };

  const handleGetVaults = async (
    chain: string,
    orderBy: string,
    selectedTVL: string,
    selectedAPY: string,
    selectedRisk: string,
    selectedTag: string[],
  ) => {
    const orderKey = orderBy.split(":")[0];
    const orderType = orderBy.split(":")[1];

    const response = await nimbus
      .get(
        `/v2/farming/vaults?chain=${chain}&order=${orderKey}&sort=${orderType}&tvl=${selectedTVL === "all" ? undefined : encodeURIComponent(selectedTVL)}&apy=${selectedAPY === "all" ? undefined : encodeURIComponent(selectedAPY)}&risk=${selectedRisk === "all" ? undefined : selectedRisk}&tags=${selectedTag.length === 0 ? [] : selectedTag}`,
      )
      .then((res: any) => res.data);
    return response;
  };

  $: queryListVaults = createQuery({
    queryKey: [
      "query-list-vaults",
      selectedChain,
      orderBy,
      selectedTVL,
      selectedAPY,
      selectedRisk,
      selectedTag,
    ],
    queryFn: () =>
      handleGetVaults(
        selectedChain,
        orderBy,
        selectedTVL,
        selectedAPY,
        selectedRisk,
        selectedTag,
      ),
    retry: false,
    enabled: Boolean(selectedChain && selectedChain?.length !== 0),
  });

  $: {
    if (
      !$queryListVaults.isError &&
      $queryListVaults.data !== undefined &&
      $queryListVaults.data.length !== 0
    ) {
      dataVaults = $queryListVaults.data;
    } else {
      dataVaults = [];
    }
  }

  let holdingTokenData: any = [];

  const handleGetHoldingTokenData = (data: any) => {
    holdingTokenData = data;
  };

  const handleSelectedChain = (value: string) => {
    selectedChain = value;
    selectedToken = {};
  };

  const handleChangeOrderBy = (value: string) => {
    orderBy = value;
  };
</script>

<ErrorBoundary>
  <div class="header-container">
    <div class="max-w-[1600px] m-auto xl:w-[82%] w-[94%]">
      <div class="flex flex-col gap-2">
        <div class="text-4xl font-semibold text-white">
          {$t("Yield farming explorer")}
        </div>
        <div class="text-xl text-white">
          Track and compare yield farming opportunities across DeFi platforms
        </div>
      </div>
    </div>
  </div>

  <div
    class="xl:min-h-screen max-w-[1600px] m-auto md:w-[94%] w-full md:-mt-26 -mt-28"
  >
    <div
      class="yield_container rounded-[20px] xl:p-8 p-4 xl:shadow-md flex flex-col gap-5"
    >
      <Filter {handleChangeFilter} {handleSelectedChain} {selectedChain} />

      <div class="grid xl:grid-cols-3 grid-cols-1 gap-6">
        <Holding
          {selectedChain}
          {selectedTokenList}
          isLoadingQueryListToken={$queryListToken.isFetching}
          {handleSelectedToken}
          {handleSelectedAddress}
          {selectedToken}
          {handleGetHoldingTokenData}
        />

        <YieldTable
          {handleChangeOrderBy}
          {dataVaults}
          isLoading={$queryListVaults.isFetching}
          {selectedToken}
          {holdingTokenData}
          {selectedChain}
        />
      </div>
    </div>
  </div>
</ErrorBoundary>

