<script lang="ts">
  import axios from "axios";
  import mixpanel from "mixpanel-browser";
  import { createQuery } from "@tanstack/svelte-query";
  import { handleValidateAddress } from "~/lib/queryAPI";
  import tooltip from "~/lib/tooltip";
  import {
    tonConnector,
    wallet,
    selectedTokenDetail,
    userPublicAddress,
    showStonfiDeFiFarmModal,
  } from "~/store";
  import { TonClient, toNano, Address } from "@ton/ton";
  import { DEX, pTON } from "@ston-fi/sdk";
  import { StonApiClient } from "@ston-fi/api";

  import Button from "~/components/Button.svelte";

  export let selectedProtocol;

  $: console.log("selectedProtocol: ", selectedProtocol);

  const stonApi = new StonApiClient();

  const client = new TonClient({
    endpoint: "https://api.dedust.io/v2/jsonRPC",
  });

  // TODO: change to selected protocol data
  const JETTON_1_ADDRESS = "EQAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAM9c"; // TON
  const JETTON_0_ADDRESS = "EQCxE6mUtQJKFnGfaROTKOt1lZbDiiX1kCixRv7Nw2Id_sDs"; // USDT

  let publicAddress = $tonConnector?.connected
    ? Address.parse($tonConnector?.account.address).toString({
        urlSafe: true,
        bounceable: true,
      })
    : $userPublicAddress;

  let isLoadingSupply = false;

  let amountToken0 = 1;
  let dataMintAmountInput = {};

  $: queryValidate = createQuery({
    queryKey: ["validate", publicAddress],
    queryFn: () => handleValidateAddress(publicAddress),
    retry: false,
  });

  const handleTONAuth = async () => {
    mixpanel.track("user_login_ton");
    if ($tonConnector?.connected) {
      $tonConnector.disconnect();
    } else {
      $tonConnector.setConnectRequestParameters({
        state: "ready",
      });

      $tonConnector.openModal();

      $tonConnector.onStatusChange((wallet) => {
        if (wallet) {
          const parserAddress = Address.parse(
            wallet?.account?.address,
          ).toString({ urlSafe: true, bounceable: true });

          publicAddress = parserAddress;
        }
      });
    }
  };

  $: isEnable = Boolean(
    $selectedTokenDetail &&
      $tonConnector &&
      $tonConnector?.connected &&
      publicAddress === $wallet &&
      $queryValidate?.data &&
      $queryValidate?.data?.type === "TON",
  );

  let selectedPoolData: any = {};

  const getSelectedPool = async () => {
    try {
      const response = await axios
        .post("https://rpc.ston.fi", {
          jsonrpc: "2.0",
          id: 179,
          method: "pool.find",
          params: {
            token_a: JETTON_1_ADDRESS,
            token_b: JETTON_0_ADDRESS,
            wallet_address: publicAddress,
          },
        })
        .then((res: any) => res.data);

      if (response && response?.result?.pools?.length !== 0) {
        selectedPoolData = response?.result?.pools.find(
          (item: any) => item.address === selectedProtocol.id,
        );
      }
    } catch (e) {
      console.error(e);
    }
  };

  $: {
    if (selectedProtocol && Object.keys(selectedProtocol)?.length !== 0) {
      getSelectedPool();
    }
  }

  // TODO: call api when user change input tokenA or tokenB (case two sides)
  const getMinUnitAmount = async (
    selectedPoolData: any,
    tokenAInput: any,
    tokenBInput: any,
  ) => {
    try {
      const response = await axios
        .post("https://rpc.ston.fi", {
          jsonrpc: "2.0",
          id: 43,
          method: "dex.simulate_liquidity_provision",
          params: {
            provision_type: "Balanced",
            router_address: selectedPoolData.router_address,
            wallet_address: publicAddress,
            slippage_tolerance: "0.01",
            token_a: JETTON_1_ADDRESS,
            token_b: JETTON_0_ADDRESS,
            token_b_units: "2000000", // incase user 2 USDT input
            token_a_units: "100000000", // incase user 0.1 TON input
          },
        })
        .then((res: any) => res.data);

      if (response && response?.result) {
        dataMintAmountInput = response?.result;
      }
    } catch (e) {
      console.error(e);
    }
  };

  // handle supply (single sides) TON/Jetton
  const handleSupplyTONJetton = async () => {
    try {
      isLoadingSupply = true;

      const pool = await stonApi.getPool(selectedProtocol.id);
      const router = await stonApi.getRouter(pool.routerAddress);

      const routerContract = client.open(
        DEX.v2_2.Router.create(router?.address),
      );

      const txParams = await routerContract
        .getSingleSideProvideLiquidityJettonTxParams({
          sendTokenAddress: JETTON_0_ADDRESS,
          otherTokenAddress: router.ptonMasterAddress, // pTON
          sendAmount: 100000n, // 0.1 USDT (! USDT has 6 decimals, do not use `toNano` function here)
          minLpOut: 1n, // minimum amount of LP tokens to receive 1 unit (means any non-zero amount)
          userWalletAddress: publicAddress, // user connected address
        })
        .catch((e) => console.error(e));

      if ($tonConnector?.connected && txParams) {
        const formatTxsParams = {
          address: txParams.to.toString(),
          value: txParams.value.toString(),
          payload: txParams.body?.toBoc().toString("base64"),
        };

        await $tonConnector.sendTransaction({
          validUntil: Date.now() + 1000000,
          messages: [formatTxsParams],
        });
      }
    } catch (e) {
      console.error(e);
    } finally {
      isLoadingSupply = false;
    }
  };

  // handle supply (two sides) TON/Jetton
  const handleSupplyTONJettonTwoSides = async () => {
    try {
      isLoadingSupply = true;

      const pool = await stonApi.getPool(selectedProtocol.id);
      const router = await stonApi.getRouter(pool.routerAddress);

      const routerContract = client.open(
        DEX.v2_1.Router.create(router?.address),
      );

      const proxyTon = pTON.v2_1.create(router.ptonMasterAddress);

      const txParams = await Promise.all([
        // deposit 0.205901460 TON to the TON/USDT pool and get at least 1 nano LP token
        routerContract.getProvideLiquidityTonTxParams({
          userWalletAddress: publicAddress,
          proxyTon,
          sendAmount: BigInt("205901460"),
          otherTokenAddress: JETTON_0_ADDRESS,
          minLpOut: 1n, // minimum amount of LP tokens to receive 1 unit (means any non-zero amount)
          queryId: 12345,
        }),
        // deposit 1 USDT to the TON/USDT pool and get at least 1 nano LP token
        routerContract.getProvideLiquidityJettonTxParams({
          userWalletAddress: publicAddress,
          sendTokenAddress: JETTON_0_ADDRESS,
          sendAmount: BigInt("999986"),
          otherTokenAddress: router.ptonMasterAddress,
          minLpOut: 1n, // minimum amount of LP tokens to receive 1 unit (means any non-zero amount)
          queryId: 123456,
        }),
      ]);

      if ($tonConnector?.connected && txParams) {
        const formatTxsParams = txParams.map((param) => {
          return {
            address: param.to.toString(),
            value: param.value.toString(),
            payload: param.body?.toBoc().toString("base64"),
          };
        });

        await $tonConnector.sendTransaction({
          validUntil: Date.now() + 1000000,
          messages: formatTxsParams,
        });
      }
    } catch (e) {
      console.error(e);
    } finally {
      isLoadingSupply = false;
    }
  };

  // handle supply (single sides) Jetton/Jetton
  const handleSupplyJettonJetton = async () => {
    try {
      isLoadingSupply = true;

      const pool = await stonApi.getPool(selectedProtocol.id);
      const router = await stonApi.getRouter(pool.routerAddress);

      const routerContract = client.open(
        DEX.v2_2.Router.create(router?.address),
      );

      const txParams = await routerContract
        .getSingleSideProvideLiquidityJettonTxParams({
          sendTokenAddress: JETTON_0_ADDRESS,
          otherTokenAddress: JETTON_1_ADDRESS,
          sendAmount: 100000n, // 0.1 USDT (! USDT has 6 decimals, do not use `toNano` function here)
          minLpOut: 1n, // minimum amount of LP tokens to receive 1 unit (means any non-zero amount)
          userWalletAddress: publicAddress, // user connected address
        })
        .catch((e) => console.error(e));

      if ($tonConnector?.connected && txParams) {
        const formatTxsParams = {
          address: txParams.to.toString(),
          value: txParams.value.toString(),
          payload: txParams.body?.toBoc().toString("base64"),
        };

        await $tonConnector.sendTransaction({
          validUntil: Date.now() + 1000000,
          messages: [formatTxsParams],
        });
      }
    } catch (e) {
      console.error(e);
    } finally {
      isLoadingSupply = false;
    }
  };

  const handleSupply = () => {
    // TODO: check selected protocol is TON/Jetton or Jetton/Jetton
    handleSupplyTONJettonTwoSides();
  };
</script>

<div class="flex flex-col gap-4">
  <div>hello world</div>

  <div class="flex justify-start gap-2">
    {#if !isEnable || (selectedPoolData && Object.keys(selectedPoolData)?.length === 0)}
      {#if (publicAddress !== $wallet || (selectedPoolData && Object.keys(selectedPoolData)?.length === 0)) && $queryValidate?.data && $queryValidate?.data?.type === "TON"}
        <div
          use:tooltip={{
            content: `<tooltip-detail text="${selectedPoolData && Object.keys(selectedPoolData)?.length === 0 ? "Missing pool data!" : "Your connected wallet is not matches with selected wallet"}" />`,
            allowHTML: true,
            placement: "top",
            interactive: true,
          }}
          class="w-[120px]"
        >
          <Button variant="disabled" disabled>
            <div class="text-white">Supply</div>
          </Button>
        </div>
      {:else}
        <div class="w-[120px]">
          <Button variant="tertiary" on:click={handleTONAuth}>
            <div class="text-white">Connect TON</div>
          </Button>
        </div>
      {/if}
    {:else}
      <div class="w-[120px]">
        {#if Number(amountToken0) > 0}
          <Button
            variant="tertiary"
            on:click={handleSupply}
            isLoading={isLoadingSupply}
            disabled={isLoadingSupply}
          >
            <div class="text-white">Supply</div>
          </Button>
        {:else}
          <div
            use:tooltip={{
              content: `<tooltip-detail text="Invalid amount!" />`,
              allowHTML: true,
              placement: "top",
              interactive: true,
            }}
          >
            <Button variant="disabled" disabled>
              <div class="text-white">Supply</div>
            </Button>
          </div>
        {/if}
      </div>
    {/if}
    <div class="w-[120px]">
      <Button
        variant="secondary"
        on:click={() => {
          showStonfiDeFiFarmModal.update((n) => (n = false));
        }}
      >
        Cancel
      </Button>
    </div>
  </div>
</div>

<style>
</style>
