<script lang="ts">
  import { isDarkMode } from "~/store";
  import { createQuery } from "@tanstack/svelte-query";
  import { nimbus } from "~/lib/network";

  import TooltipNumber from "~/components/TooltipNumber.svelte";
  import Button from "~/components/Button.svelte";

  export let selectedProtocol;
  export let selectedChain;

  let selectedTokenList: any = [];
  let amount = "";

  let tokenPrice = {
    tokenA: 0,
    tokenB: 0,
  };

  const handleQueryListToken = async (chain: string) => {
    const response = await nimbus
      .get(`/onchain/coins?chain=${chain}`)
      .then((res) => res?.data);
    return response;
  };

  $: queryListToken = createQuery({
    queryKey: ["query-list-token-by-chain", selectedChain],
    queryFn: () => handleQueryListToken(selectedChain),
    retry: false,
    enabled: Boolean(selectedChain && selectedChain?.length !== 0),
  });

  $: {
    if (!$queryListToken.isError && $queryListToken.data !== undefined) {
      selectedTokenList = $queryListToken.data;
    }
  }

  $: formatSelectedProtocol =
    selectedChain === "SUI"
      ? selectedTokenList && selectedTokenList?.length !== 0
        ? {
            ...selectedProtocol,
            pairs: [
              {
                symbol: selectedProtocol?.name,
                ca: selectedProtocol?.coins[0],
                info:
                  selectedTokenList.find(
                    (eachToken: any) =>
                      eachToken?.address === selectedProtocol?.coins[0],
                  ) ||
                  selectedTokenList.find(
                    (eachToken: any) =>
                      eachToken?.symbol === selectedProtocol?.name,
                  ),
              },
            ],
          }
        : {}
      : selectedTokenList && selectedTokenList?.length !== 0
        ? {
            ...selectedProtocol,
            pairs: selectedProtocol?.name
              ?.split("-")
              ?.map((item: string, index: number) => {
                const info =
                  selectedTokenList.find(
                    (eachToken: any) =>
                      eachToken?.address === selectedProtocol?.coins[index],
                  ) ||
                  selectedTokenList.find(
                    (eachToken: any) =>
                      eachToken?.symbol === (item === "USD" ? "USDT" : item),
                  );

                return {
                  symbol: item,
                  ca: selectedProtocol?.coins[index],
                  info,
                };
              }),
          }
        : {};

  $: percentPriceChangeTokenA =
    tokenPrice?.tokenA !== 0
      ? ((tokenPrice?.tokenA -
          formatSelectedProtocol?.pairs[0]?.info?.priceUSD) /
          Math.abs(formatSelectedProtocol?.pairs[0]?.info?.priceUSD)) *
        100
      : 0;

  $: percentPriceChangeTokenB =
    tokenPrice?.tokenB !== 0
      ? ((tokenPrice?.tokenB -
          formatSelectedProtocol?.pairs[1]?.info?.priceUSD) /
          Math.abs(formatSelectedProtocol?.pairs[1]?.info?.priceUSD)) *
        100
      : 0;
</script>

<div class="flex flex-col gap-2">
  <div class="text-2xl font-medium">Simulator</div>

  <div class="flex flex-col gap-4">
    <div class="flex flex-col gap-1">
      <div class="text-base">Networth Investment</div>
      <div
        class="flex items-center gap-1 border border_0000000d py-2 px-3 rounded-[10px]"
      >
        <div class="text-lg">$</div>
        <div class="flex-1">
          <input
            type="text"
            placeholder="0"
            autocomplete="off"
            inputmode="decimal"
            class="text-xl p-0 border-none focus:outline-none focus:ring-0 flex-1 bg-transparent"
            value={amount}
            on:change={(event) => {
              amount = event?.target?.value;
            }}
          />
        </div>
      </div>
    </div>

    {#if selectedTokenList && selectedTokenList?.length !== 0 && formatSelectedProtocol && Object.keys(formatSelectedProtocol).length !== 0}
      <div class="grid grid-cols-2 gap-4">
        <div
          class="border border_0000000d py-2 px-3 rounded-[10px] flex flex-col gap-3"
        >
          <div class="text-base">Current Price</div>

          {#if selectedChain === "SUI"}
            <div class="flex flex-col gap-1">
              <div class="text-gray-500 text-sm">
                {formatSelectedProtocol?.name} Price (USD)
              </div>
              <div
                class={`flex items-center gap-1 border border_0000000d py-2 px-3 rounded-[10px] ${$isDarkMode ? "bg-black/50" : "bg-gray-100/50"}`}
              >
                <div class="text-lg">$</div>
                <div class="flex-1">
                  <input
                    disabled
                    type="text"
                    placeholder="0"
                    autocomplete="off"
                    inputmode="decimal"
                    class="text-xl p-0 border-none focus:outline-none focus:ring-0 flex-1 bg-transparent"
                    value={formatSelectedProtocol?.pairs[0]?.info?.priceUSD}
                  />
                </div>
              </div>
            </div>
          {:else}
            {#each formatSelectedProtocol?.pairs as pair}
              <div class="flex flex-col gap-1">
                <div class="text-gray-500 text-sm">
                  {pair?.symbol} Price (USD)
                </div>
                <div
                  class={`flex items-center gap-1 border border_0000000d py-2 px-3 rounded-[10px] ${$isDarkMode ? "bg-black/50" : "bg-gray-100/50"}`}
                >
                  <div class="text-lg">$</div>
                  <div class="flex-1">
                    <input
                      disabled
                      type="text"
                      placeholder="0"
                      autocomplete="off"
                      inputmode="decimal"
                      class="text-xl p-0 border-none focus:outline-none focus:ring-0 flex-1 bg-transparent"
                      value={pair?.info?.priceUSD}
                    />
                  </div>
                </div>
              </div>
            {/each}
          {/if}
        </div>

        <div
          class="border border_0000000d py-2 px-3 rounded-[10px] flex flex-col gap-2"
        >
          <div class="text-base">Future Price</div>

          {#if selectedChain === "SUI"}
            <div class="flex flex-col gap-1">
              <div class="text-gray-500 text-sm flex items-center">
                {formatSelectedProtocol?.name} Price (
                <div class="flex items-center">
                  <TooltipNumber
                    number={percentPriceChangeTokenA}
                    type="percent"
                  />%
                </div>
                )
              </div>
              <div
                class="flex items-center gap-1 border border_0000000d py-2 px-3 rounded-[10px]"
              >
                <div class="text-lg">$</div>
                <div class="flex-1">
                  <input
                    type="text"
                    placeholder="0"
                    autocomplete="off"
                    inputmode="decimal"
                    class="text-xl p-0 border-none focus:outline-none focus:ring-0 flex-1 bg-transparent"
                    value={formatSelectedProtocol?.pairs[0]?.info?.priceUSD}
                    on:change={(event) => {
                      tokenPrice = {
                        tokenA: event?.target?.value,
                        tokenB: tokenPrice.tokenB,
                      };
                    }}
                  />
                </div>
              </div>
            </div>
          {:else}
            {#each formatSelectedProtocol?.pairs as pair, index}
              <div class="flex flex-col gap-1">
                <div class="text-gray-500 text-sm flex items-center">
                  {pair?.symbol} Price (
                  <div class="flex items-center">
                    {#if index === 0}
                      <TooltipNumber
                        number={percentPriceChangeTokenA}
                        type="percent"
                      />%
                    {:else}
                      <TooltipNumber
                        number={percentPriceChangeTokenB}
                        type="percent"
                      />%
                    {/if}
                  </div>
                  )
                </div>

                <div
                  class="flex items-center gap-1 border border_0000000d py-2 px-3 rounded-[10px]"
                >
                  <div class="text-lg">$</div>
                  <div class="flex-1">
                    <input
                      type="text"
                      placeholder="0"
                      autocomplete="off"
                      inputmode="decimal"
                      class="text-xl p-0 border-none focus:outline-none focus:ring-0 flex-1 bg-transparent"
                      value={pair?.info?.priceUSD}
                      on:change={(event) => {
                        if (index === 0) {
                          tokenPrice = {
                            tokenA: event?.target?.value,
                            tokenB: tokenPrice.tokenB,
                          };
                        } else {
                          tokenPrice = {
                            tokenA: tokenPrice.tokenA,
                            tokenB: event?.target?.value,
                          };
                        }
                      }}
                    />
                  </div>
                </div>
              </div>
            {/each}
          {/if}
        </div>
      </div>
    {/if}

    <div class="w-max">
      {#if amount}
        <Button
          variant="tertiary"
          on:click={() => {
            if (selectedChain === "SUI") {
              if (tokenPrice.tokenA === 0) {
                tokenPrice = {
                  tokenA: formatSelectedProtocol?.pairs[0]?.info?.priceUSD,
                  tokenB: tokenPrice.tokenB,
                };
              }
            } else {
              if (tokenPrice.tokenA === 0) {
                tokenPrice = {
                  tokenA: formatSelectedProtocol?.pairs[0]?.info?.priceUSD,
                  tokenB: tokenPrice.tokenB,
                };
              }
              if (tokenPrice.tokenB === 0) {
                tokenPrice = {
                  tokenA: tokenPrice.tokenA,
                  tokenB: formatSelectedProtocol?.pairs[1]?.info?.priceUSD,
                };
              }

              if (tokenPrice.tokenA === 0 && tokenPrice.tokenB === 0) {
                tokenPrice = {
                  tokenA: formatSelectedProtocol?.pairs[0]?.info?.priceUSD,
                  tokenB: formatSelectedProtocol?.pairs[1]?.info?.priceUSD,
                };
              }
            }

            console.log("amount: ", amount);
            console.log("tokenPrice: ", tokenPrice);
          }}
        >
          Simulate
        </Button>
      {:else}
        <Button variant="disabled" disabled>Simulate</Button>
      {/if}
    </div>
  </div>
</div>

<style></style>
